import {
  Cleanup,
  effect,
  scoped,
} from "https://raw.githubusercontent.com/mini-jail/signal/main/mod.ts";

let parentAttrs: Object | undefined;
let parentNodes: DOMNode[] | undefined;
let parentElt: DOMElement | undefined;
const keys = new Set<string>();

export function attrsRef():
  | ElementAttributes & EventAttributes<HTMLElement>
  | undefined;
export function attrsRef<T extends keyof HTMLElementTagNameAttributeMap>():
  | HTMLElementTagNameAttributeMap[T]
  | undefined;
export function attrsRef<T extends keyof SVGElementTagNameAttributeMap>():
  | SVGElementTagNameAttributeMap[T]
  | undefined;
export function attrsRef(): Object | undefined {
  if (parentElt === undefined) return undefined;
  if (parentAttrs === undefined) parentAttrs = Object.create(null);
  return parentAttrs;
}

export function elementRef(): HTMLElement | undefined;
export function elementRef(): SVGElement | undefined;
export function elementRef<T extends keyof HTMLElementTagNameMap>():
  | HTMLElementTagNameMap[T]
  | undefined;
export function elementRef<T extends keyof SVGElementTagNameMap>():
  | SVGElementTagNameMap[T]
  | undefined;
export function elementRef(): HTMLElement | SVGElement | undefined {
  return parentElt;
}

export function element<T extends keyof HTMLElementTagNameAttributeMap>(
  tagName: T,
): void;
export function element<T extends keyof HTMLElementTagNameAttributeMap>(
  tagName: T,
  options: HTMLElementTagNameAttributeMap[T],
): void;
export function element<T extends keyof HTMLElementTagNameAttributeMap>(
  tagName: T,
  options: (attributes: HTMLElementTagNameAttributeMap[T]) => void,
): void;
export function element<T extends keyof HTMLElementTagNameAttributeMap>(
  tagName: T,
  options?: any,
): void {
  const elt: DOMElement = document.createElement(<string> tagName);
  if (options) modify(elt, options);
  insert(elt);
}

export function elementNS<T extends keyof SVGElementTagNameAttributeMap>(
  tagName: T,
): void;
export function elementNS<T extends keyof SVGElementTagNameAttributeMap>(
  tagName: T,
  options: SVGElementTagNameAttributeMap[T],
): void;
export function elementNS<T extends keyof SVGElementTagNameAttributeMap>(
  tagName: T,
  options: (attributes: SVGElementTagNameAttributeMap[T]) => void,
): void;
export function elementNS<T extends keyof SVGElementTagNameAttributeMap>(
  tagName: T,
  options?: any,
): void {
  const elt: DOMElement = document.createElementNS(
    "http://www.w3.org/2000/svg",
    <string> tagName,
  );
  if (options) modify(elt, options);
  insert(elt);
}

export function text(data: any): void {
  const node = new Text();
  modifyChar(node, data);
  insert(node);
}

export function comment(data: any): void {
  const node = new Comment();
  modifyChar(node, data);
  insert(node);
}

function modifyChar(node: CharacterData, data: any): void {
  if (typeof data === "function") {
    effect<string>((current) => {
      const next = data();
      if (next !== current) node.data = String(next);
      return next;
    });
  } else {
    node.data = String(data);
  }
}

function modify(elt: DOMElement, options: any): void {
  if (typeof options === "object") {
    return attributes(elt, undefined, options);
  }
  elt.append("");
  effect<[attr?: Record<string, any>, nodes?: Node[]] | undefined>((curr) => {
    const newNodes: Node[] = parentNodes = [];
    parentAttrs = options.length ? Object.create(null) : undefined;
    parentElt = elt;
    options(parentAttrs);
    if (curr?.[1]?.length || newNodes.length) {
      union(elt.firstChild!, curr?.[1], newNodes);
    }
    parentElt = undefined;
    parentNodes = undefined;
    if (curr?.[0] || parentAttrs) {
      attributes(elt, curr?.[0], parentAttrs);
    }
    parentAttrs = undefined;
    if (parentAttrs || newNodes.length) {
      return [parentAttrs, newNodes];
    }
  });
}

function insert(node: Node): void {
  if (parentNodes) parentNodes.push(node);
  else parentElt?.appendChild(node);
}

function union(
  anchor: DOMNode,
  curr: (DOMNode | undefined)[] | undefined,
  next: DOMNode[],
): void {
  const elt = anchor.parentNode!;
  if (curr === undefined) {
    for (const node of next) {
      elt.insertBefore(node, anchor);
    }
    return;
  }
  const currLength = curr.length;
  const nextLength = next.length;
  let currNode: DOMNode | undefined, i: number, j: number;
  outerLoop:
  for (i = 0; i < nextLength; i++) {
    currNode = curr[i];
    for (j = 0; j < currLength; j++) {
      if (curr[j] === undefined) continue;
      else if (curr[j]!.nodeType === 3 && next[i].nodeType === 3) {
        if (curr[j]!.data !== next[i].data) curr[j]!.data = next[i].data;
        next[i] = curr[j]!;
      } else if (curr[j]!.isEqualNode(next[i])) next[i] = curr[j]!;
      if (next[i] === curr[j]) {
        curr[j] = undefined;
        if (i === j) continue outerLoop;
        break;
      }
    }
    elt.insertBefore(next[i], currNode?.nextSibling || null);
  }
  while (curr.length) curr.pop()?.remove();
}

function qualifiedName(name: string): string {
  return name
    .replace(/([A-Z])/g, (match) => "-" + match[0])
    .toLowerCase();
}

function eventName(name: string): string {
  return name.startsWith("on:") ? name.slice(3) : name.slice(2).toLowerCase();
}

function attribute(elt: DOMElement, key: string, value: any): void {
  if (key === "text" || key === "textContent") {
    if (elt.firstChild?.nodeType === 3) {
      (elt.firstChild as Text).data = String(value);
    } else {
      elt.prepend(String(value));
    }
  } else if (key in elt) {
    elt[key] = value;
  } else if (value !== undefined) {
    elt.setAttributeNS(null, qualifiedName(key), String(value));
  } else {
    elt.removeAttributeNS(null, qualifiedName(key));
  }
}

function attributes(elt: DOMElement, curr?: Object, next?: Object): void {
  keys.clear();
  curr && Object.keys(curr).forEach((key) => keys.add(key));
  next && Object.keys(next).forEach((key) => keys.add(key));
  for (const key of keys) {
    const nextValue = next?.[key];
    const currValue = curr?.[key];
    if (nextValue === undefined && currValue === undefined) {
      continue;
    }
    if (key.startsWith("on") && currValue !== nextValue) {
      currValue && elt.removeEventListener(eventName(key), curr[key]);
      nextValue && elt.addEventListener(eventName(key), next[key]);
    } else if (typeof nextValue === "function") {
      effect((current) => {
        const value = nextValue();
        if (value !== current) attribute(elt, key, value);
        return value;
      });
    } else if (typeof nextValue === "object") {
      for (const subKey in nextValue) {
        if (typeof nextValue[subKey] === "function") {
          effect((current) => {
            console.log(nextValue[subKey]);
            const value = nextValue[subKey]();
            if (value !== current) {
              elt[key][subKey] = value || null;
            }
            return value;
          });
        } else {
          elt[key][subKey] = nextValue[subKey] || null;
        }
      }
    } else {
      attribute(elt, key, next?.[key]);
    }
  }
}

export function mount(elt: DOMElement, callback: () => void): Cleanup {
  return scoped((cleanup) => {
    modify(elt, callback);
    return cleanup;
  })!;
}

